.Footer {
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: #201e1f;
}
.Footer p {
  color: white;
  font-size: 1em;
}
.Footer a {
  color: dodgerblue;
}
.Footer .LicenseLogo {
  display: inline-block;
  border: none;
  height: 1em;
  opacity: 0.45;
  -webkit-transition: 0.36s;
  -o-transition: 0.36s;
  transition: 0.36s;
}
.Footer .LicenseLogo:hover {
  opacity: 1;
}



