/* Make the NEXT element in the page give us space. */

.Navbar {
  position: fixed;
  z-index: 1;
  left: 0;

	top: -80px;

	border-bottom: 1px solid black;

  display: -ms-flexbox;

  display: flex;

  height: 50px;

  -webkit-animation: appear 1s ease-in forwards;

          animation: appear 1s ease-in forwards;

  position: fixed;

  width: 100%;
}

@-webkit-keyframes appear {
  0% {
    top: -80px;
  }
  100% {
    top: 0;
  }
}

@keyframes appear {
  0% {
    top: -80px;
  }
  100% {
    top: 0;
  }
}



